import FileSaver from "file-saver";
import { Base64 } from "js-base64";
import React, { useRef, useState } from "react";
import { Button, Card, Overlay, Stack, Tooltip } from "react-bootstrap";
import { FaCopy, FaSave } from "react-icons/fa";
import headerUtil from '../utils/header-util';

export interface OutputData {
    type: string;
    format: string;
    data: string | Uint8Array;
}

export default (props: {
    output: OutputData;
}) => {
    const copyTarget = useRef(null);
    const saveTarget = useRef(null);
    const [copyTooltipVisible, setCopyTooltipVisible] = useState(false);
    const [saveTooltipVisible, setSaveTooltipVisible] = useState(false);

    const handleCopy = () => {
        switch (props.output.type) {
            case 'text':
                navigator.clipboard.writeText(props.output.data as string);
                break;
            case 'image':
                let imageBlob = new Blob([props.output.data], { type: "image/*" });
                navigator.clipboard.write([new ClipboardItem({ "image/*": imageBlob })]);
                break;
            case 'hex':
                break;
            case 'bin':
                break;
            default:
                console.log('Invalid format.', props.output.type)
        }

        setCopyTooltipVisible(true)
        setTimeout(() => setCopyTooltipVisible(false), 500);
    };

    const handleSave = () => {
        switch (props.output.type) {
            case 'text':
                let textBlob = new Blob([props.output.data], { type: "text/plain;charset=utf-8" });
                FileSaver.saveAs(textBlob, "output.txt");
                break;
            case 'image':
                let imageBlob = new Blob([props.output.data], { type: "image/*" });
                const ext = props.output.format === 'auto' ? headerUtil(props.output.data as Uint8Array) : props.output.format;
                FileSaver.saveAs(imageBlob, "image." + ext);
                break;
            case 'hex':
                break;
            case 'bin':
                break;
            default:
                console.log('Invalid format.', props.output.type)
        }

        setSaveTooltipVisible(true);
        setTimeout(() => setSaveTooltipVisible(false), 500);
    };

    const getContent = () => {
        switch (props.output.type) {
            case 'text':
                return props.output.data;
            case 'image':
                let imageBlob = new Blob([props.output.data], { type: "application/octet-stream" });
                console.log('>>>', imageBlob.type)
                return <img src={URL.createObjectURL(imageBlob)} style={{ maxWidth: "100%" }} />;
            case 'hex':
                break;
            case 'bin':
                break;
            default:
                return 'Unknown type';
        }
    };

    return (
        <Card>
            <Card.Header>
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    Decoded output
                    <Stack direction='horizontal' gap={3} >
                        <Button ref={copyTarget} variant="primary" onClick={handleCopy}> <FaCopy />  Copy to clipboard </Button>
                        <Button ref={saveTarget} variant="primary" onClick={handleSave}> <FaSave />  Save as file </Button>
                        <Overlay target={copyTarget.current} show={copyTooltipVisible} placement="top" >
                            {(props) => (
                                <Tooltip id="copy" {...props} >
                                    Copied
                                </Tooltip>
                            )}
                        </Overlay>
                        <Overlay target={saveTarget.current} show={saveTooltipVisible} placement="top">
                            {(props) => (
                                <Tooltip id="save" {...props}>
                                    Saved
                                </Tooltip>
                            )}
                        </Overlay>
                    </Stack>
                </div>
            </Card.Header>
            <Card.Body>
                {getContent()}
            </Card.Body>
        </Card>
    );
}