import React, { Fragment, useState } from "react";
import { useEffect } from "react";
import { Stack, DropdownButton, Dropdown, ToggleButtonGroup, ToggleButton, Row, Container } from "react-bootstrap";

import TEXT_ENCODINGS from '../assets/config/encodings.json';
import IMAGE_FORMATS from '../assets/config/image-formats.json';

export default (props: {
    onTypeChanged: (type: string, format: string) => void;
}) => {
    const TYPES = [
        {
            id: 'text',
            label: 'Text',
            jsx: (textEncoding: typeof TEXT_ENCODINGS[0]) => (
                <Stack direction="horizontal" gap={3}>
                    <span style={{ display: 'inline-block' }}>Text encoding:</span>
                    <DropdownButton id="text-encoding-dropdown" variant="outline-dark" title={textEncoding.name} onSelect={handleTextEncodingClick} >
                        {
                            TEXT_ENCODINGS.map(encoding => <Dropdown.Item key={encoding.code + '-' + encoding.name} eventKey={JSON.stringify(encoding)} >{encoding.name}</Dropdown.Item>)
                        }
                    </DropdownButton>
                </Stack >
            )
        },
        {
            id: 'image',
            label: 'Image',
            jsx: (imageFormat: typeof IMAGE_FORMATS[0]) => {
                return (
                    <Stack direction="horizontal" gap={3}>
                        <span style={{ display: 'inline-block' }}>Image format:</span>
                        <DropdownButton id="image-format-dropdown" variant="outline-dark" title={imageFormat.name} onSelect={handleImageFormatClick} >
                            {
                                IMAGE_FORMATS.map(encoding => <Dropdown.Item key={encoding.code + '-' + encoding.name} eventKey={JSON.stringify(encoding)} >{encoding.name}</Dropdown.Item>)
                            }
                        </DropdownButton>
                    </Stack >
                )
            }
        },
        {
            id: 'hex',
            label: 'Hexadecimal',
            jsx: () => null
        },
        {
            id: 'bin',
            label: 'Binary',
            jsx: () => null
        }
    ];

    const [type, setType] = useState(TYPES[0]);
    const [format, setFormat] = useState(TEXT_ENCODINGS[0]);

    const handleTextEncodingClick = (selection: any) => {
        const format = JSON.parse(selection);
        if (format) {
            setFormat(format);
        }
    }

    const handleImageFormatClick = (selection: any) => {
        const format = JSON.parse(selection);
        if (format) {
            setFormat(format);
        }
    }

    useEffect(() => {
        props.onTypeChanged(type.id, format.code);
    }, [type, format]);

    console.log('>>>', type, format)

    return (
        <Container>
            <Row>
                <p>
                    <Stack direction="horizontal" gap={3}>
                        <span style={{ paddingRight: '10px' }}>Outout type:</span>
                        <ToggleButtonGroup type='radio' name='outputType' value={type.id}>
                            {TYPES.map(type => (
                                <ToggleButton key={type.id} variant="outline-dark" value={type.id} onClick={() => {
                                    setType(type)

                                    switch (type.id) {
                                        case 'text':
                                            setFormat(TEXT_ENCODINGS[0]);
                                            break;
                                        case 'image':
                                            setFormat(IMAGE_FORMATS[0]);
                                            break;
                                    }
                                }}>{type.label}</ToggleButton>
                            ))}
                        </ToggleButtonGroup>
                    </Stack>
                </p>
            </Row>
            <Row>
                <p>
                    {type.jsx(format)}
                </p>
            </Row>
        </Container>
    );
};