

export default (buffer: Uint8Array) => {
    let offset = 0;
    if (check(0, [0xEF, 0xBB, 0xBF], buffer)) { // UTF-8-BOM
        offset = 3;
    }

    if (check(offset, [0x42, 0x4D], buffer)) {
        return 'bmp';
    }
    if (check(offset, [0x47, 0x49, 0x46], buffer)) {
        return 'gif';
    }
    if (check(offset, [0xFF, 0xD8, 0xFF], buffer)) {
        return 'jpg';
    }
    if (check(offset, [0x52, 0x49, 0x46, 0x46, undefined, undefined, undefined, undefined, 0x57, 0x45, 0x42, 0x50, 0x56, 0x50], buffer)) {
        return 'webp';
    }
    if (check(offset, [0x89, 0x50, 0x4E, 0x47, 0x0D, 0x0A, 0x1A, 0x0A], buffer)) {
        return 'png';
    }

    return undefined;
};

const check = (offset: number, match: (number | undefined)[], buffer: Uint8Array): boolean => {
    for (let i = 0; i < buffer.length; i++) {
        if (match[i] && buffer.at(i + offset) != match[i]) {
            return false;
        }
    }

    return true;
}